@font-face {
  font-family: "URWGeometric-Bold.woff";
  src: url("/URWGeometric-Bold.woff") format("woff");
}

p {
  margin: 0px;
  padding: 0px;
}

.about-text p {
  font-size: 1.7em;
  line-height: 1.5em;
}

.background {
  background: #FCEADE;
  width: 100%;
  height: 1800px;
  position: relative;
}

.nav-links-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  padding: 0 200px;
  margin-bottom: 100px;
}

.navlink-txt {
  font-family: "URWGeometric-Bold.woff";
  font-size: 50px;
}

.title-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: 100%;
  text-align: center;
  margin-top: 80px;
  z-index: 10;
  position: absolute;
}

.hero-txt {
  margin: 0px;
  font-family: "URWGeometric-Bold.woff";
  font-size: 140px;
  line-height: 140px;
  color: black;
  display: inline-block;
}

.hero-txt:hover {
  -webkit-animation: textanimation 1.2s ease infinite;
          animation: textanimation 1.2s ease infinite;
  cursor: crosshair;
}

@-webkit-keyframes textanimation {
  25%  {
    color:#16F4D0;
    background-position:0% 50%;}
  50%  {
    color:#FF8A5B;
    background-position:100% 50%;}
  100% {
    color:#16F4D0;
    background-position:0% 50%;}
}

@keyframes textanimation {
  25%  {
    color:#16F4D0;
    background-position:0% 50%;}
  50%  {
    color:#FF8A5B;
    background-position:100% 50%;}
  100% {
    color:#16F4D0;
    background-position:0% 50%;}
}

.about-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0px;
  width: 100%;
  margin-top: 300px;
}

.anna-img {
  height: 700px;
  margin-bottom: 30px;
}

.about-text {
  font-family: "URWGeometric-Bold.woff";
  font-size: 20px;
  line-height: 28px;
  text-align: left;
  width: 800px;
}

.heart-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: start;
      align-items: flex-start;
  height: 10%;
  width: 50px;
  margin-left: 200px;
  margin-top: 45px;
}

.heart-container img {
  width: 50px;
}

.heart-img:hover {
  -webkit-animation: shake 0.7s;
          animation: shake 0.7s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.heart-info {
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg);
  text-align: center;
  width: 150px;
  font-family: 'IBM Plex Mono', monospace;
  font-size: 12px;
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

.heart-info.hidden {
  display: none;
}

.invisible-scroll-arrow {
  width: 100%;
  height: 250px;
  position: absolute;
  top: 700px;
  z-index: 10;
}

.invisible-scroll-arrow:hover {
  cursor: url("/scroll-blk.png"), auto;
}

.social-links-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: end;
      align-items: flex-end;
  width: 100%;
  padding-right: 20px;
  margin-top: 200px;
  position: absolute;
}

.social-links img {
  width: 25px;
  height: 25px;
  background-color: black;
  margin: 2px 0px;
  position: relative;
  z-index: 20;
}



